.hero {
  height: 878px;

  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(180deg, rgba(242,249,255,0.12) 0%, #F2F9FF);

  &__heading {
    @extend %h1;
    margin-top: 144px;
  }

  &__text {
    @extend %text;
    margin-top: 27px;
    line-height: 150%;
    letter-spacing: normal;
  }

  &__actions {
    margin-top: 46px;
    display: flex;
  }

  &__digits {
    @extend %pseudobutton-blue;
    padding: 17px 38px;
  }

  &__features {
    @extend %pseudobutton-white;
    margin-left: 22px;
    padding: 17px 40px;
  }
  
  &__illustration {
    width: 977px;
    height: 585px;
    display: block;
    margin-top: 32px;
  }
}