@import "nav";

.header {
  height: 80px;
  padding: 0 210px;

  display: flex;
  align-items: center;

  font-family: "Halyard Text";
  font-size: 16px;
  color: #1e266d;

  background: #fff;

  &__logo {
    height: 28px;
    display: block;
    margin-right: 85px;
  }

  &__nav {
    margin-right: 244px;
  }

  &__phone {
    margin-right: 34px;
  }

  &__action {
    @extend %pseudobutton-blue;
    padding: 8px 17px;
  }
}
