%h1 {
  @include heading;
  font-size: 56px;
  line-height: 105%;
}

%h2 {
  @include heading;
  font-size: 48px;
  line-height: 125%;
}

%h3 {
  @include heading;
  font-size: 22px;
  line-height: 125%;
}

%text {
  font-size: 18px;
  line-height: 150%;
  color: $regular-color;
}

%pseudobutton-blue {
  @include pseudobutton;
  color: #fff;
  background-color: #3751ff;
}

%pseudobutton-buy-digits {
  @extend %pseudobutton-blue;
  padding: 17px 38px;
}

%pseudobutton-white {
  @include pseudobutton;
  color: #455880;
  background-color: #fff;
}
