.nav {
  width: 500px;

  display: flex;
  justify-content: space-between;

  &__item {
    line-height: 80px;
    position: relative;

    &_arrow {
      width: 86px;
      background: url(/images/arrow-down.svg) no-repeat;
      background-position-x: right;
      background-position-y: center;
    }
  }
}