.examples {
  height: 539px;
  margin-top: 27px;
  display: grid;
  grid-template-columns: repeat(5, 311px);
  grid-column-gap: 20px;

  &__block {
    display: grid;
    grid-template-columns: 311px;
    grid-template-rows: repeat(3, 231px);
    grid-row-gap: 20px;
    position: relative;

    &_1 {
      top: 0px;
    }

    &_2 {
      top: 114px;
    }

    &_3 {
      top: 171px;
    }

    &_4 {
      top: 231px;
    }

    &_5 {
      top: 114px;
    }
  }
}