@import "examples";

.portfolio {
  margin-top: 190px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #FFF9EA;

  &__heading {
    @extend %h2;
    margin-top: 139px;
  }

  &__text {
    @extend %text;
    margin-top: 40px;
    font-size: 21px;
    text-align: center;
  }

  &__digits {
    margin-top: 40px;
    @extend %pseudobutton-buy-digits;
  }
}