@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin heading {
  font-family: "Halyard Display";
  text-align: center;
  color: $heading-color;
}

@mixin pseudobutton {
  border-radius: $pseudobtn-border-rds;
  font-size: 16px;
}
